import React from 'react';
import './App.css';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import {Howl} from 'howler';
import chest0 from './img/Chest1.png';
import chest1 from './img/Chest2.png';
import chest2 from './img/Chest3.png';
import chest3 from './img/Chest4.png';
import success from './sound/PICKLOCK_SUCCESS.mp3';
import failure from './sound/PICKLOCK_FAILURE.mp3';
import open from './sound/OPEN_CHEST.mp3';
import close from './sound/CLOSE_CHEST.mp3';
import coin from './img/items/gold.png';
import meat from './img/items/meat.png';
import cookedmeat from './img/items/cooked_meat.png';
import hp1 from  './img/items/PotionLife1.png';
import hp2 from  './img/items/PotionLife2.png';
import hp3 from  './img/items/PotionLife3.png';
import hp4 from  './img/items/PotionLife4.png';
import ma1 from  './img/items/PotionMana1.png';
import ma2 from  './img/items/PotionMana2.png';
import ma3 from  './img/items/PotionMana3.png';
import ma4 from  './img/items/PotionMana4.png';
import sp from  './img/items/PotionSpeed2.png';
import de from  './img/items/PotionDexterity.png';
import str from  './img/items/PotionStrength.png';
import Leftstats from './components/leftstats.js';
import Eq from './components/eq.js';
import BottomShow from './components/bottomshow.js';

let items = [
    {id:0,name: 'Zoto',img: coin,features: [],value: 1},
    {id:1,name: 'Surowe Miso',img: meat,features: [["Premia punktw trafie",6]],value: 3},
    {id:2,name: 'Smaone Miso',img: cookedmeat,features: [["Premia punktw trafie",12]],value: 6},
    {id:3,name: 'Esencja Lecznicza',img: hp1,features: [["Premia punktw trafie",50]],value: 25},
    {id:4,name: 'Ekstrakt Leczniczy',img: hp2,features: [["Premia punktw trafie",70]],value: 35},
    {id:5,name: 'Eliksir Leczniczy',img: hp3,features: [["Premia punktw trafie",100]],value: 505},
    {id:6,name: 'Eliksir ycia',img: hp4,features: [["Premia do maks. punktw trafie",20]],value: 1500},
    {id:7,name: 'Esencja Many',img: ma1,features: [["Premia many",50]],value: 25},
    {id:8,name: 'Ekstrakt Many',img: ma2,features: [["Premia many",75]],value: 40},
    {id:9,name: 'Eliksir Many',img: ma3,features: [["Premia many",100]],value: 60},
    {id:10,name: 'Eliksir Ducha',img: ma4,features: [["Premia do maks. many",5]],value: 1500},
    {id:11,name: 'Mikstura szybkoci',img: sp,features: [["Tymczasowo zwiksza twoj szybko",""],["Czas trwania (min.)",5]],value: 200},
    {id:12,name: 'Eliksir Zrcznoci',img: de,features: [["Premia do zrcznoi",3]],value: 800},
    {id:13,name: 'Eliksir Siy',img: str,features: [["Premia do siy",3]],value: 800},
];
class App extends React.Component {

state ={
  comb: [],
  pos: 0,
  CaseAnimation: false,
  TextAnimation: false,
  prevMove: "",
  prevLock: "",
  itemShow: -1,
  eqOpen: false,
  equip: [],
  whichChest: chest0,
  otherItems: []
}
lockState(){
  if(this.state.prevLock === "success") return 'Brzmi niele';
  else if(this.state.prevLock === "failure") return 'Cholera... Sprbuj ponownie';
  else return null;
}
checkWin(){
  if((this.state.pos === this.state.comb.length) && (this.state.pos > 0) && !this.state.chestOpen){
    let sound = new Howl({src: open});
    sound.play();
    this.createItems();
    this.setState({
      eqOpen: true,
      chestOpen: true
    });
  }
}
createItems(){
  let copyState = [];
  let itCOUNT;
  if(this.state.whichChest===chest0) itCOUNT=1;
  else if(this.state.whichChest===chest1) itCOUNT=2;
  else if(this.state.whichChest===chest2) itCOUNT=3;
  else if(this.state.whichChest===chest3) itCOUNT=4;
  else itCOUNT=1;
  for(let i=0; i<itCOUNT; i++){
    let element = Math.floor(Math.random()*items.length);
    let count = itCOUNT;
    let item = copyState.find(it => it.id === element);

   if(item === undefined) copyState.push({id: element,count: count});
   else item.count += count;
  }
  copyState.sort((a,b)=>(a.id - b.id));
  this.setState({otherItems: copyState});
}
closeChest(){
  if(this.state.chestOpen){
    let sound = new Howl({src: close});
    sound.play();
    this.makeGame();
    this.setState({
      eqOpen: false,
      chestOpen: false
    });
}}
handleKeyDown(key,e){
  if(!this.state.eqOpen){
  let {comb,pos} = this.state;
    if(comb[pos]===key){
      let sound = new Howl({src: success});
      sound.play();
      this.setState((prevState) => ({
        pos: prevState.pos + 1,
        prevMove: key,
        prevLock: "success",
        TextAnimation: true,
        CaseAnimation: true
      }));
    }else if(key==="left" || key==="right"){
      let sound = new Howl({src: failure});
      sound.play();
      this.setState({
        pos: 0,
        prevMove: key,
        prevLock: "failure",
        TextAnimation: true,
        CaseAnimation: true
      });
    }
  }
  if(this.state.chestOpen){
    if(key==='alt'){
      e.preventDefault();
      if(this.state.otherItems.length){
        this.moveItems(this.state.otherItems[0].id,1);
      }
    }
  }
  if(key==='esc'){
    e.preventDefault();
    this.closeChest();
  }
    if(key==='tab'){
      e.preventDefault();
      this.setState((prevState) => ({
        eqOpen: !prevState.eqOpen
      }));
    }
  }
  makeGame(){
    let ch = Math.floor(Math.random()*4);
    let che;
    let combCount;
    if(ch===0) {che= chest0;combCount=3;}
    else if(ch===1){ che= chest1;combCount=4;}
    else if(ch===2){ che= chest2;combCount=6;}
    else if(ch===3){ che= chest3;combCount=8;}
    else{ che= chest0;combCount=3;}

  let comb = [];
  for(let i=0; i<combCount; i++) comb.push(Math.floor(Math.random()*2)? "left":"right");

  this.setState({
    pos: 0,
    prevMove: '',
    prevLock: '',
    comb,
    whichChest: che
  });
  }
  chestText(){
    if(this.state.whichChest === chest0) return <div className="zwykla">Zwyka skrzynia</div>;
    else if(this.state.whichChest === chest1) return <div className="srebrna">Srebrna skrzynia</div>;
    else if(this.state.whichChest === chest2) return <div className="zlota">Zota skrzynia</div>;
    else if(this.state.whichChest === chest3) return <div className="epicka">Epicka skrzynia</div>;
    else return <div className="zwykla">Zwyka skrzynia</div>;
  }
  showItem=(a)=>{
    this.setState({
      itemShow: a,
    });
  }
  moveItems=(id,fast)=>{
    let copyOtherItems = JSON.parse(JSON.stringify(this.state.otherItems));
    let copyEQ = JSON.parse(JSON.stringify(this.state.equip));

    let rItem = copyOtherItems.find(it=> it.id===id);
    let aItem = copyEQ.find(it=> it.id===id);
  if(!fast){
    if(aItem  === undefined) copyEQ.push({id: id,count: 1});
    else aItem.count +=1;
    rItem.count-=1;
  }else{
    if(aItem  === undefined) copyEQ.push({id: id,count: rItem.count});
    else aItem.count +=rItem.count;
    rItem.count=0;
  }
    copyOtherItems = copyOtherItems.filter(it=> it.count > 0);
    if(copyOtherItems.length===0) this.setState({itemShow: -1});

    copyOtherItems.sort((a,b)=>(a.id - b.id));
    copyEQ.sort((a,b)=>(a.id - b.id));
    this.setState({
      otherItems: copyOtherItems,
      equip: copyEQ
    });
  }
  hideItem=()=>{
    this.setState({
      itemShow: -1,
    });
  }
  componentDidMount(){
    this.makeGame();
  }
  componentDidUpdate(){
     this.checkWin();
  }
  render(){
    let {prevLock,prevMove,CaseAnimation,TextAnimation} = this.state;
    return (
      <div className="App c" style={{flexDirection: "column"}}>
        <KeyboardEventHandler handleKeys={['left', 'right','tab','esc','alt']} onKeyEvent={(key, e) => this.handleKeyDown(key,e)} />
        <Leftstats/>
        <div
          onAnimationEnd={() => this.setState({ TextAnimation: false })}
          className={TextAnimation ? `statusA status status_${prevLock}` : `status status_${prevLock}`}>{this.lockState()}
        </div>
        <img
          alt="Skrzynia"
          src={this.state.whichChest}
          onAnimationEnd={() => this.setState({ CaseAnimation: false })}
          className={CaseAnimation ? `anm_${prevMove} chest` : 'chest'}
        />
        {this.chestText()}
         <img
          alt="Skrzynia"
          src={this.state.whichChest}
          className="chest2"
        />
        {this.state.eqOpen && <Eq equip={this.state.equip} items={items} itemShow={this.showItem} itemHide={this.hideItem} theme="R"/>}
        {this.state.chestOpen > 0 && <Eq equip={this.state.otherItems} items={items} itemShow={this.showItem} itemHide={this.hideItem} theme="L" click={this.moveItems}/>}
        {(this.state.itemShow > -1) && <BottomShow item={this.state.itemShow} items={items}/>}
        {(this.state.chestOpen && this.state.otherItems <=0)&&<button className="close" onClick={()=> this.closeChest()}>Zamknij</button>}
      </div>
    );
  }
}

export default App;
