import React from 'react';
import './eq.css';
import EqItem from './eqitem.js';


const Eq = (props)=>{
      let eqlist = props.equip.map((item,index) =>(
        <EqItem
          idx={item.id}
          key={index}
          source={props.items[item.id].img}
          count={item.count} name={item.name}
          itemShow={props.itemShow}
          itemHide={props.itemHide}
          click={props.click}
        />
      ));
      if(eqlist.length%5){
          let sup = 5 - eqlist.length%5;
        for(let i=0; i< sup;i++) eqlist.push(<EqItem key={eqlist.length}/>);
      }
      while(eqlist.length < 30) eqlist.push(<EqItem key={eqlist.length}/>);
      let gold = props.equip.find(it=> it.id === 0);
        return (
          <>
            {props.theme === "R" && <div className="gold">Zoto: {gold ? gold.count: "0  "}</div>}
            <div className={`c equ equ${props.theme}`}>
                {eqlist}
            </div>
          </>
        )
}

export default Eq;